.stars {
  display: flex;

  &__star {
    background-image: url(/images/star.svg);
    background-position: center;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    margin-right: 4px;

  }

  &--4 .stars__star:nth-child(-n+4) {
    background-image: url(/images/star-active.svg);
  }
}
